
import { defineComponent, computed, ref, inject } from 'vue';

export default defineComponent({
    props: {
        optionsList: {
            type: Array as () => string[],
            required: true,
        },
        value: {
            type: String,
            required: true,
        }
    },
    setup(props, { emit }) {
        const settings = inject('settings');

        const valueHandler = {
            get() {
                return props.value;
            }, 
            set(val: string) {
                emit('update:modelValue', val);
            },
        }
    
        const sele = ref('');

        // @ts-ignore
        const selectedVal = computed(valueHandler);
        return {
            settings,
            selectedVal,
            sele
        }
    }
});
