
import { defineComponent, inject } from 'vue';

export default defineComponent({
    props: {
        title: {
            type: String,
            required: false
        },
        listIterable: {
            type: Array as () => string[],
            required: true,
        },
        slots: {
            type: Number,
            required: false,
            default: 4
        },
        selected: {
            type: Number,
            required: false,
            default: -1
        }
    },
    setup(props) {
        const settings = inject('settings');

        // const freeSlots = computed(() => {
        const freeSlots = () => {
            // @ts-ignore
            return props.listIterable.length - props.slots;
        };

        return {
            settings,
            freeSlots,
        }
    }
});
