
import { computed, defineComponent } from 'vue'
import Icon from '@/components/Icon/Icon.vue';

export default defineComponent({
    components: {
        Icon,
    },
    props: {
        text: {
            type: String,
            required: false,
        },
        icon: {
            type: String,
            required: false,

        },
        color: {
            type: String,
            required: false,
            default: 'currentColor'
        },
        size: {
            type: Number,
            required: false,
            default: 16,
        },
        expand: {
            type: Boolean,
            required: false,
            default: false,
        },
        round: {
            type: Boolean,
            required: false,
            default: false,
        },
        onClickFunction: {
            type: Function,
            required: true,
        },
        active: {
            type: Boolean,
            required: false,
            default: false
        }
    },
    setup(props) {
        const style = computed(() => {
            return {
                //@ts-ignore
                width: (props.expand) ? `${ props.size * 2}px` : 'auto',
                //@ts-ignore
                height: (props.expand) ? `${ props.size * 2}px` : 'auto',
                fontSize: `${ props.size}px`,
                color: props.color,
            }
        });
        return {
            style,
        }
    }
});
