
import { defineComponent } from 'vue';

export default defineComponent({
	props: {
		showing: {
			type: Boolean,
			required: true,
			default: false,
		},
	},
	setup(props, { emit }) {

		const close = () => {
			console.log('Modal closed');
			emit('update:showing', false);
			emit('close');
		}

		return {
			close,
		}
	}
})
